import React, { useState, useEffect } from "react";

function Slider() {
  const [current, setCurrent] = useState<number>(0);
  const data = [
    {
      url:
        "http://demo.posthemes.com/pos_ecolife/layout2/modules/posslideshows/images/03c53493b66e3228091fb5027e13aa8e78207328_1_2.jpg",
      title: "COFFEE CREAMS & CHOCOLATE",
      post: "Girella Motta Cacao",
      post2: "Soft Sponge Cake",
    },
    {
      url:
        "http://demo.posthemes.com/pos_ecolife/layout2/modules/posslideshows/images/081b9c1b802e10a8438349ca084ec5451f955c8e_2_2.jpg",
      title: "CACAO FRUITTI ROSSI",
      post: "La Girela Motta",
      post2: "Diventa Rosa",
    },
  ];

  useEffect(() => {
    setTimeout(() => {
      current === 1 ? setCurrent(0) : setCurrent(1);
    }, 5000);
  });

  return (
    <React.Fragment>
      <div className="line"></div>
      <div
        className={
          current === 0
            ? "slider animation-slider1"
            : "slider animation-slider2"
        }
      >
        <img src={data[current].url} alt="pictureslider" />
        <div className="slider-content">
          <div className="slider-title">{data[current].title}</div>
          <div className={current === 0 ? "slider-main" : "slider-main"}>
            <h2>{data[current].post}</h2>
            <h2>{data[current].post2}</h2>
          </div>
          <button>SHOP NOW</button>
        </div>
        <div className="slider-direct">
          <div
            className={
              current === 1 ? "direct" : "direct active-slider animation-slider"
            }
            onClick={() => {
              setCurrent(0);
            }}
          ></div>
          <div
            className={
              current === 0 ? "direct" : "direct active-slider animation-slider"
            }
            onClick={() => {
              setCurrent(1);
            }}
          ></div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default Slider;
