import React, { useState, useEffect } from "react";
import axios, { AxiosResponse } from 'axios';
import { render } from "react-dom";
// @ts-ignore
import ReactStars from "react-rating-stars-component";
import { useSelector,useDispatch } from 'react-redux';
import {fetchProductsPending,fetchProductsSuccess,fetchProductsError} from "./product.action";

type Props = {};
type ProductInfo = {
    "id": string,
    "cover": string,
    "name": string,
    "price": string,
    "sales": string,
    "stars": string,
    "type": string,
};

function ProductList(props: Props) {
    const dispatch = useDispatch();
    const products : any = useSelector((state : any) => state.product.products);
    const pending : any = useSelector((state : any) => state.product.pending);
    const pendingFail : any = useSelector((state:any ) => state.product.error);
    // const [products, setProducts] = useState<ProductInfo[]>([]);
    async function getProductList() {
        try {
            dispatch(fetchProductsPending());
            const data = await axios.get("/products");
            dispatch(fetchProductsSuccess(data.data));
        }
        catch (e) {
            dispatch(fetchProductsError(e));
        }
    }
    //componentDidMount
    useEffect(() => {
        getProductList();
    }, [])
    return (
        <div className="product-list">
            <div className="container">
                <div className="row ">
                    {pending && "...Loading"}
                    {pendingFail && "...There is no data"}
                    {!pending && !pendingFail && products.map((product : any) => {
                        var saleStringNoPercentage = parseFloat(product.sales.toString().substr(1, product.sales.toString().length - 1)).toFixed(1);
                        var saleStringWithPercentage = parseFloat(product.sales.toString().substr(1, product.sales.toString().length - 2)).toFixed(1);
                        var productPrice = parseFloat((product.price.toString())).toFixed(1);
                        var priceNoPercentage = parseFloat(productPrice) - parseFloat(saleStringNoPercentage);
                        var priceWithPercentage = parseFloat(productPrice) - parseFloat(productPrice) / 100 * parseFloat(saleStringWithPercentage);
                        var lastPriceNoPercentage = priceNoPercentage.toFixed(1);
                        var lastPricePercentage = priceWithPercentage.toFixed(1);
                        if (product.sales.toString().charAt(0) === "0") {
                            return (
                                <div className="product-card">
                                    <img src={product.cover} alt="Avatar" style={{ width: "100%" }} />
                                    <div className="product-desc">
                                        <h4><b>{product.name}</b></h4>
                                        <ReactStars
                                            count={5}
                                            size={24}
                                            edit={false}
                                            emptyIcon={<i className="far fa-star"></i>}
                                            value={product.stars}
                                            fullIcon={<i className="fa fa-star"></i>}
                                            activeColor="#ffd700"
                                        />
                                        <div className="product-price">
                                            <div className="new-price">
                                                ${product.price}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        }
                        if (product.sales.toString().charAt(product.sales.toString().length - 1) != "%") {
                            return (
                                <div className="product-card">
                                    <img src={product.cover} alt="Avatar" style={{ width: "100%" }} />
                                    <div className="product-desc">
                                        <h4><b>{product.name}</b></h4>
                                        <ReactStars
                                            count={5}
                                            size={24}
                                            edit={false}
                                            emptyIcon={<i className="far fa-star"></i>}
                                            value={product.stars}
                                            fullIcon={<i className="fa fa-star"></i>}
                                            activeColor="#ffd700"
                                        />
                                        <div className="product-price">
                                            <div className="last-price">
                                                ${product.price}
                                            </div>
                                            <div className="new-price">
                                                ${lastPriceNoPercentage}
                                            </div>
                                            <div className="sales-rate">
                                                {product.sales}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            );
                        }
                        if (product.sales.toString().charAt(product.sales.toString().length - 1) === "%") {
                            return (
                                <div className="product-card">
                                    <img src={product.cover} alt="Avatar" style={{ width: "100%" }} />
                                    <div className="product-desc">
                                        <h4><b>{product.name}</b></h4>
                                        <ReactStars
                                            count={5}
                                            size={24}
                                            edit={false}
                                            emptyIcon={<i className="far fa-star"></i>}
                                            value={product.stars}
                                            fullIcon={<i className="fa fa-star"></i>}
                                            activeColor="#ffd700"
                                        />
                                        <div className="product-price">
                                            <div className="last-price">
                                                ${product.price}
                                            </div>
                                            <div className="new-price">
                                                ${lastPricePercentage}
                                            </div>
                                            <div className="sales-rate">
                                                {product.sales}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            );
                        }
                    })}
                    
                </div>
            </div>
        </div>)
}
export default ProductList;