import React, { useState, useEffect } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link, useLocation, useHistory
} from "react-router-dom";
import routeNavBar from "./RouteNavBar";

type Props = {};

function HomeButton() {
    let history = useHistory();

    function handleClick() {
        history.push("/home");
    }
    return (
        <button type="button" onClick={handleClick}>
            Go home
        </button>
    );
}

function NavBarItems(props: Props) {
    const { pathname } = useLocation();
    //check with ? if there is a title label => direct -- from typescript 4 
    const titlelabel = routeNavBar[pathname]?.title;
    //or : console titlelable = routeNavBar[pathname] ? routeNavBar[pathname].title : "";
    return (
        <div className="page-header">
            <div className="navigation-bar">
                <div className="logo">
                    <img src="/logo/ecolife-logo.jpg" />
                </div>
                <div className="navigation-bar-item">
                    <div className="navbar">
                        <ul className="navbar-ul">
                            {Object.keys(routeNavBar).map((route) => {
                                const { title, subtitle } = routeNavBar[route];
                                if (subtitle) {
                                    return (
                                        <li className="nav-title">
                                            <div className="dropdown">
                                                <ul>
                                                    <Link className={`nav-link dropbtn ${pathname === route ? 'active' : ""}`} to={route}>
                                                        {title}
                                                        <i className="fa fa-caret-down"></i>
                                                    </Link>
                                                    <div className="dropdown-content">
                                                        {Object.keys(subtitle).map((route) => {
                                                            return (
                                                                <li>
                                                                    <Link className={`nav-link ${pathname === route ? 'active' : ""}`} to={route}>
                                                                        {subtitle[route].title}
                                                                    </Link>
                                                                </li>
                                                            );
                                                        })}
                                                    </div>
                                                </ul>
                                            </div>
                                        </li>
                                    );
                                }
                                return (
                                    <li className="nav-title">
                                        <Link className={`nav-link ${pathname === route ? 'active' : ""}`} to={route}>
                                            {title}
                                        </Link>
                                    </li>
                                );
                            })}


                        </ul>
                    </div>

                </div>
                <div className="navbar-right-side">
                    <a className="w3-xlarge"><i className="fa fa-shopping-cart"></i></a>
                    <a className="w3-xlarge"><i className="fa fa-search" /></a>
                </div>
            </div>
        </div>
    )
}
export default NavBarItems;